<template>
    <div class="mu-container" v-if="show">
        <BackTop></BackTop>
        <LightGallery
                :images="previewImages"
                :index="previewIndex"
                :disable-scroll="true"
                @close="previewIndex = null"
        />
        <Row :gutter="16">
            <Col :xs="24" :sm="24" :md="24" :lg="{ span: 6, offset: 1 }" :xl="{ span: 4, offset: 5 }"
                 :xxl="{ span: 4, offset: 5 }">
                <Affix :offset-top="10" v-if="!isMobile">
                    <div class="mu-user">
                        <m-user :uuid="uuid"></m-user>
                    </div>
                    <div class="mu-photo">
                        <m-photo :uuid="uuid"></m-photo>
                    </div>
                </Affix>
            </Col>
            <Col :xs="24" :sm="24" :md="24" :lg="{ span: 16}" :xl="{ span: 10}" :xxl="{ span: 10 }">
                <div class="mu-user" v-if="isMobile" style="margin-bottom: 10px;">
                    <m-user :uuid="uuid"></m-user>
                </div>
                <div class="mu-tab" v-if="show">
                    <Tabs :animated="false" :value="activeTab">
                        <TabPane label="时刻" icon="md-list" name="list">
                            <m-blank v-if="momentList.length === 0"></m-blank>
                            <m-item :moment-date="item.format_date.date" :moment-week="item.format_date.week"
                                    :moment-time="item.created_at" :moment-desc="item.content"
                                    v-for="(item, index) in momentList">
                                <div v-if="item.type === 'image'" id="lightgallery">
                                    <div v-if="item.image.length === 1">
                                        <img :src="item.image[0].url+'?imageView2/1/w/1600/h/1000'" alt=""
                                             class="mu-card__image" @click="previewImage(index, 0)">
                                    </div>
                                    <div v-else-if="item.image.length % 2 === 0">
                                        <div v-for="(imageItem, imageIndex) in item.image.length / 2">
                                            <Row :gutter="4">
                                                <Col span="12">
                                                    <img :src="item.image[imageIndex * 2].url+'?imageView2/1/w/800/h/500'"
                                                         alt="" class="mu-card__image"
                                                         @click="previewImage(index, imageIndex * 2)">
                                                </Col>
                                                <Col span="12">
                                                    <img :src="item.image[imageIndex * 2 + 1].url+'?imageView2/1/w/800/h/500'"
                                                         alt="" class="mu-card__image"
                                                         @click="previewImage(index, imageIndex * 2 + 1)">
                                                </Col>
                                            </Row>
                                        </div>
                                    </div>
                                    <div v-else-if="item.image.length % 3 === 0">
                                        <div v-for="(imageItem, imageIndex) in item.image.length / 3">
                                            <Row :gutter="4">
                                                <Col span="8">
                                                    <img :src="item.image[imageIndex * 3].url+'?imageView2/1/w/800/h/800'"
                                                         alt="" class="mu-card__image"
                                                         @click="previewImage(index, imageIndex * 3)">
                                                </Col>
                                                <Col span="8">
                                                    <img :src="item.image[imageIndex * 3 + 1].url+'?imageView2/1/w/800/h/800'"
                                                         alt="" class="mu-card__image"
                                                         @click="previewImage(index, imageIndex * 3 + 1)">
                                                </Col>
                                                <Col span="8">
                                                    <img :src="item.image[imageIndex * 3 + 2].url+'?imageView2/1/w/800/h/800'"
                                                         alt="" class="mu-card__image"
                                                         @click="previewImage(index, imageIndex * 3 + 2)">
                                                </Col>
                                            </Row>
                                        </div>
                                    </div>
                                </div>
                                <div v-else-if="item.type === 'video'" class="mu-card__video">
                                    <m-video :source="item.video.url"></m-video>
                                </div>
                                <div v-else-if="item.type === 'location'">
                                    <img :src="$global.requestUrl+'/moment.map?latitude='+item.location.amap_gps.latitude+'&longitude='+item.location.amap_gps.longitude"
                                         alt="" class="mu-card__image">
                                </div>
                            </m-item>
                            <div class="mu-nav" v-if="momentList.length > 0">
                                <Button type="primary" :loading="loading" shape="circle" @click="bindMoreMoment">
                                    <span v-if="!loading">{{showMore?'加载更多':'暂无更多'}}</span>
                                    <span v-else>正在加载</span>
                                </Button>
                            </div>
                        </TabPane>
                        <TabPane label="位置" icon="md-locate" name="location">
                            <m-map :uuid="uuid"></m-map>
                        </TabPane>
                    </Tabs>
                </div>
            </Col>
        </Row>
        <m-footer></m-footer>
    </div>
</template>

<script>
    // @ is an alias to /src
    import {getMomentList} from '@/api/moment';
    import MomentUser from '@/components/MomentUser';
    import MomentItem from '@/components/MomentItem';
    import MomentVideo from '@/components/MomentVideo';
    import MomentPhoto from '@/components/MomentPhoto';
    import MomentMap from '@/components/MomentMap';
    import MomentFooter from '@/components/MomentFooter';
    import MomentBlank from '@/components/MomentBlank';
    import {LightGallery} from 'vue-light-gallery';

    export default {
        name: 'home',
        data() {
            return {
                momentList: [],
                activeTab: 'list',
                show: false,
                loading: false,
                showMore: true,
                uuid: "",
                page: 1,
                pageSize: 10,
                navButton: "i-icon-arrow-down",
                previewImages: [],
                previewIndex: null,
                isMobile: this.$isMobile
            }
        },
        components: {
            'm-user': MomentUser,
            'm-item': MomentItem,
            'm-video': MomentVideo,
            'm-photo': MomentPhoto,
            'm-map': MomentMap,
            'm-footer': MomentFooter,
            'm-blank': MomentBlank,
            LightGallery
        },
        created() {
            this.$Spin.show();
            if (typeof this.$route.query.uuid == 'undefined' || this.$route.query.uuid === '') {
                window.location.href = '#/welcome';
            } else {
                this.uuid = this.$route.query.uuid;
                this.requestMoment();
            }
            this.$Notice.info({
                title: '欢迎来到时刻Lite',
                desc: '你可以使用「时刻Lite」小程序来记录时刻'
            });
        },methods: {
            /**
             * 请求获取时刻列表
             */
            requestMoment() {
                this.$Loading.start();
                getMomentList({
                    'uuid': this.uuid,
                    page: this.page,
                    page_size: this.pageSize
                }).then((res) => {
                    if (res.data.status === 200) {
                        this.momentList = this.momentList.concat(res.data.data.list);
                        if (res.data.data.page === res.data.data.pages) {
                            this.showMore = false;
                        }
                    }
                    this.show = true;
                    this.loading = false;
                    this.$Spin.hide();
                    this.$Loading.finish();
                })
            },

            /**
             * 点击Tab按钮
             * @param tab
             * @param event
             */
            handleClick(tab, event) {
                console.log(tab, event);
            },

            /**
             * 加载更多时刻
             */
            bindMoreMoment() {
                this.page++;
                this.loading = true;
                this.requestMoment();
            },

            /**
             * 预览图片
             * @param index
             * @param imageIndex
             */
            previewImage(index, imageIndex) {
                this.previewImages = this.momentList[index]['image'];
                this.previewIndex = imageIndex;
            }
        }
    }
</script>
