<template>
    <div class="mu-photo">
        <LightGallery
                :images="previewImages"
                :index="previewIndex"
                :disable-scroll="true"
                @close="previewIndex = null"
        />
        <Row>
            <Col span="12"><h2>相册</h2></Col>
            <Col span="12" style="text-align: right;padding-right: 10px;">
                <h2 @click="nextGalleryList">
                    <Icon type="md-swap"/>
                </h2>
            </Col>
        </Row>
        <div>
            <m-blank v-if="galleryList.length === 0"></m-blank>
            <Spin fix v-if="spinShow"></Spin>
            <Row :gutter="8" v-for="(item, index) in galleryList.length / 2">
                <Col span="12">
                    <img :src="galleryList[index * 2]['url'] + '?imageView2/1/w/400/h/400'"
                         alt="" @click="previewGallery(index * 2)">
                </Col>
                <Col span="12">
                    <img :src="galleryList[index * 2 + 1]['url'] + '?imageView2/1/w/400/h/400'"
                         alt="" @click="previewGallery(index * 2 + 1)">
                </Col>
            </Row>
        </div>
    </div>
</template>

<script>
    import {getUserGallery} from '@/api/user';
    import {LightGallery} from 'vue-light-gallery';
    import MomentBlank from '@/components/MomentBlank';

    export default {
        name: "MomentPhoto",
        data: function () {
            return {
                galleryList: [],
                page: 1,
                pageSize: 6,
                more: true,
                spinShow: true,
                previewImages: [],
                previewIndex: null,
            };
        },
        props: {
            uuid: String,
        },
        components: {
            LightGallery,
            'm-blank': MomentBlank
        },
        created() {
            this.getUserGallery();
        },
        watch: {
            uuid() {
                this.getUserGallery();
            }
        },
        methods: {
            /**
             * 获取用户相册
             */
            getUserGallery() {
                getUserGallery({
                    'uuid': this.uuid,
                    'page': this.page,
                    'page_size': this.pageSize
                }).then((res) => {
                    if (res.data.status === 200) {
                        this.galleryList = res.data.data.list;
                        if (parseInt(res.data.data.page) === parseInt(res.data.data.pages)) {
                            this.more = false;
                        } else {
                            this.more = true;
                            this.page++;
                        }
                    }
                    this.spinShow = false;
                })
            },

            /**
             * 更换相册内容
             */
            nextGalleryList() {
                if (!this.more) {
                    this.page = 1;
                }
                this.spinShow = true;
                this.getUserGallery();
            },

            /**
             * 预览图片
             */
            previewGallery(index) {
                this.previewImages = this.galleryList;
                this.previewIndex = index;
            }
        }
    }
</script>

<style scoped>
    h2 {
        margin-bottom: 10px;
    }

    .mu-photo {
        padding: 10px;
        background: #fff;
        position: relative;
        border-radius: 3px;
    }

    img {
        width: 100%;
        height: auto;
        border-radius: 6px;
        cursor: pointer;
    }
</style>