<template>
    <div class="mu-user__card">
        <div class="mu-user__bg"></div>
        <div class="mu-user__avatar" :style="{backgroundImage:'url(' + userAvatar + ')'}"></div>
        <div class="mu-user__bd">
            <div class="mu-user__desc">在{{userInfo.register_time}}加入时刻</div>
            <Row :gutter="20">
                <Col span="12">
                    <div class="mu-user__count">
                        <p>MOMENT</p>
                        <h2>{{userInfo.count.moment}}</h2>
                    </div>
                </Col>
                <Col span="12">
                    <div class="mu-user__count">
                        <p>PHOTO</p>
                        <h2>{{userInfo.count.image}}</h2>
                    </div>
                </Col>
            </Row>
        </div>
    </div>
</template>

<script>
    import {getUserInfo} from '@/api/user';

    export default {
        name: "MomentUser",
        data: function () {
            return {
                userInfo: {
                    "register_time": 'N/A',
                    count: {
                        moment: 0,
                        image: 0
                    }
                },
                userAvatar: 'https://cdn.mayuko.cn/moment/20190428154821.jpg'
            };
        },
        watch: {
            uuid() {
                this.getUserInfo();
            }
        },
        props: {
            uuid: String,
        },
        created() {
            this.getUserInfo();
        },
        methods: {
            getUserInfo() {
                getUserInfo({
                    'uuid': this.uuid
                }).then((res) => {
                    if (res.data.status === 200) {
                        this.userInfo = res.data.data;
                        this.userAvatar = res.data.data['avatar_url'];
                    }
                })
            }
        }
    }
</script>

