import axios from 'axios';
import global from '@/utils/global';

export function getMomentList(params) {
    return new Promise((resolve, reject) => {
        axios.get(global.requestUrl + '/web.moment.list', {
            params: params
        })
            .then(res => {
                resolve(res)
            })
            .catch(err => {
                reject(err)
            })
    })
}

