<template>
    <div class="mu-map" :style="style">
        <el-amap class="mu-amap" vid="amap-vue" ref="map" :amap-manager="amapManager" :center="center" :zoom="zoom">
            <el-amap-marker v-for="(marker, index) in locationList" :vid="index"
                            :position="marker.position"></el-amap-marker>
        </el-amap>
    </div>
</template>

<script>
    import {getUserLocation} from '@/api/user';
    import {amapManager} from 'vue-amap';

    export default {
        name: "MomentMap",
        props: {
            uuid: String,
        },
        data: function () {
            return {
                amapManager,
                zoom: 5,
                center: [115.47752, 35.163075],
                locationList: [],
                style: {
                    height: (document.body.clientHeight  - 152) + 'px'
                }
            };
        },
        mounted() {
            getUserLocation({
                'uuid': this.uuid,
            }).then((res) => {
                if (res.data.status === 200) {
                    this.locationList = this.format(res.data.data);
                }
            });
        },
        methods: {
            format(list) {
                let markers = [];
                for (let i in list) {
                    markers.push({
                        position: [list[i].amap_gps.longitude, list[i].amap_gps.latitude],
                    })
                }

                return markers;
            }
        }
    }
</script>

<style scoped>
    .mu-amap {
        width: 100%;
    }
</style>