<template>
    <VueDPlayer ref="player" :options="options"></VueDPlayer>
</template>

<script>
    import VueDPlayer from 'vue-dplayer'
    import 'vue-dplayer/dist/vue-dplayer.css'

    export default {
        name: "MomentVideo",
        props: {
            source: String
        },
        data() {
            return {
                player: null,
                options: {
                    video: {
                        url: '',
                        autoplay: false,
                        preload: 'none',
                        theme: '#ffffff',
                    }
                }
            }
        },
        created() {
            this.options.video.url = this.source;
        },
        components: {
            VueDPlayer
        }
    }
</script>

