import axios from 'axios';
import global from '@/utils/global';

export function getUserInfo(params) {
    return new Promise((resolve, reject) => {
        axios.get(global.requestUrl + '/web.user.info', {
            params: params
        })
            .then(res => {
                resolve(res)
            })
            .catch(err => {
                reject(err)
            })
    })
}

