import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router);

export default new Router({
    mode: 'hash',
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/welcome',
            name: 'welcome',
            component: () => import('./views/Welcome.vue')
        },
        {
            path: '/moment',
            name: 'moment',
            component: () => import('./views/Moment.vue')
        }
    ]
})
